import request from '@/utils/request';

// 审阅模式获取采购订单
export function getPurchaseOrderData(params) {
  return request({ url: `/purchase_order/getData`, method: 'get', params })
}

// 批量采购模式 获取待采购产品供应商列表
export function getWaitForPurchaseOrderSupplier(params) {
  return request({ url: `/purchase_order/getWaitForPurchaseOrderSupplier`, method: 'get', params })
}

// 批量采购模式 根据 平台、供应商 获取采购订单数据
export function getDataInPurchaseMode(params) {
  return request({ url: `/purchase_order/getDataInPurchaseMode`, method: 'get', params })
}

// 根据ozon订单信息更新采购订单
export function upDatePurchaseOrder(data) {
  return request({ url: `/purchase_order/updateData`, method: 'post', data })
}

// 手动修改采购单信息
export function modifyData(data) {
  return request({ url: `/purchase_order/modifyData`, method: 'post', data })
}

// 运营手动修改采购单信息（回复留言） 
export function operationModifyData(data) {
  return request({ url: `/purchase_order/operationModifyData`, method: 'post', data })
}


// 批量给采购单 填写国内运单号
export function fillThePurchaseIdForPurchaseOrders(data) {
  return request({ url: `/purchase_order/fillThePurchaseIdForPurchaseOrders`, method: 'post', data })
}

// 给采购单 填写订单号
export function fillThePurchaseIdForPurchaseOrder(data) {
  return request({ url: `/purchase_order/fillThePurchaseIdForPurchaseOrder`, method: 'post', data })
}


// 作废采购单
export function cancleThePurchaseOrder(data) {
  return request({ url: `/purchase_order/cancleThePurchaseOrder`, method: 'post', data })
}

// 更新1688订单
export function updatePurchaseOrdersIn1688WithAPI(data) {
  return request({ url: `/purchase_order/updatePurchaseOrdersIn1688WithAPI`, method: 'post', data })
}

// 批量下单1688订单 
export function buyPurchaseProductIn1688WithAPI(data) {
  return request({ url: `/purchase_order/buyPurchaseProductIn1688WithAPI`, method: 'post', data })
}

// 通过excel更新采购单信息
// 上传 Excel 文件并更新采购单信息
export function updatePurchaseOrdersWithExcel(file) {
  const formData = new FormData();
  formData.append('file', file);

  return request({
    url: '/purchase_order/updatePurchaseOrdersWithExcel',
    method: 'post',
    headers: {
      'Content-Type': 'multipart/form-data',
    },
    data: formData,
  });
}

// 上传图片
export function uploadImage(data) {
  return request({
    url: `/pic/upload`,
    method: 'post',
    data,
    headers: {
      'Content-Type': 'multipart/form-data',
    },
  });
}

// ----------------------------- 采购商品 ----------------------------- 
export function getPurchaseProductData(params) {
  return request({ url: `/purchase_product/getData`, method: 'get', params })
}

// 运营手动修改采购单信息（回复留言） 
export function addPurchaseProduct(data) {
  return request({ url: `/purchase_product/addData`, method: 'post', data })
}


